___CSS_LOADER_EXPORT___.push([module.id, `.home-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: calc(100vh - 80px);
  padding: 40px 24px 32px;
  background: linear-gradient(180deg, #f6fbff 0%, #ffffff 45%);
  text-align: center;
}

.home-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  max-width: 360px;
}

.home-title {
  font-size: 28px;
  font-weight: 600;
  color: #1d2939;
}

.home-goal-button {
  background: #1a73e8;
  color: #ffffff;
  border: none;
  border-radius: 18px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  max-width: 280px;
  transition: background 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.home-goal-button:active {
  transform: scale(0.98);
}

.home-goal-button:hover {
  background: #1866d2;
}

.home-goal-status {
  font-size: 18px;
  font-weight: 500;
  color: #1d2939;
  padding: 12px 18px;
  border-radius: 14px;
  background: rgba(26, 115, 232, 0.08);
  width: 100%;
  max-width: 320px;
}

.home-goal-status--success {
  background: rgba(16, 185, 129, 0.12);
  color: #047857;
}

.home-goal-progress {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
  color: #475467;
}

.home-illustration {
  margin-top: auto;
  display: flex;
  justify-content: center;
  width: 100%;
}

.home-illustration-image {
  max-width: 220px;
  width: 100%;
  height: auto;
}

@media (max-width: 380px) {
  .home-page {
    padding: 32px 20px 24px;
  }

  .home-title {
    font-size: 24px;
  }

  .home-goal-status {
    font-size: 16px;
  }
}




