___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Inter', 'SF UI Display', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffffff;
  min-height: 100vh;
  overflow-x: hidden;
}

.app-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: 16px;
  color: #1f2937;
  padding: 24px;
  text-align: center;
}

.sync-status {
  position: fixed;
  left: 50%;
  bottom: 96px;
  transform: translateX(-50%);
  background: rgba(30, 41, 59, 0.92);
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 999px;
  font-size: 13px;
  line-height: 1.4;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.2);
  z-index: 1000;
  pointer-events: none;
  transition: opacity 0.2s ease;
}

.sync-status--error {
  background: rgba(220, 38, 38, 0.9);
}

.sync-status--syncing {
  background: rgba(37, 99, 235, 0.9);
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: #ffffff;
}

.app-content {
  flex: 1;
  padding-bottom: 80px; /*    */
  overflow: hidden;
  height: calc(100vh - 80px);
}

/*    */
.text-center {
  text-align: center;
}

