import { DEFAULT_HOME_STATE } from '../types/home';
import { DEFAULT_SOCIAL_STATE } from '../types/social';
import { DEFAULT_USER_ID, getActiveUser, getUserScopedStorageKey } from './userIdentity';
const SYNC_DEBOUNCE_MS = 1000;
let activityData = {};
const cloneAchievementFlag = (flag) => ({
    unlocked: flag.unlocked,
    unlockedAt: flag.unlockedAt,
});
const cloneAchievements = (achievements) => ({
    firstGoalCompleted: cloneAchievementFlag(achievements.firstGoalCompleted),
    focusEightHours: cloneAchievementFlag(achievements.focusEightHours),
    sleepSevenNights: cloneAchievementFlag(achievements.sleepSevenNights),
});
const cloneGoals = (goals) => {
    const result = {};
    Object.entries(goals ?? {}).forEach(([key, goal]) => {
        if (!goal) {
            return;
        }
        result[key] = { ...goal };
    });
    return result;
};
const cloneHomeState = (state) => ({
    currentStreak: state.currentStreak,
    lastProcessedDate: state.lastProcessedDate,
    currency: state.currency,
    goals: cloneGoals(state.goals),
    achievements: cloneAchievements(state.achievements),
});
let homeState = cloneHomeState(DEFAULT_HOME_STATE);
let socialState = {
    friends: [],
    friendRequests: [],
    notifications: [],
};
let syncStatus = {
    isSyncing: false,
    lastSyncedAt: null,
};
let activeUserId = DEFAULT_USER_ID;
let initialized = false;
let syncTimeout;
let syncPending = false;
const listeners = new Set();
const stateListeners = new Set();
const cloneActivityData = (data) => JSON.parse(JSON.stringify(data ?? {}));
const cloneSocialState = (state) => ({
    friends: state.friends.map((friend) => ({ ...friend })),
    friendRequests: state.friendRequests.map((request) => ({ ...request })),
    notifications: state.notifications.map((notification) => ({
        ...notification,
        payload: notification.payload ? { ...notification.payload } : undefined,
    })),
});
socialState = cloneSocialState(DEFAULT_SOCIAL_STATE);
const notifyListeners = () => {
    listeners.forEach((listener) => {
        try {
            listener({ ...syncStatus });
        }
        catch (error) {
            console.error('Error notifying sync listener:', error);
        }
    });
};
const notifyStateChange = () => {
    stateListeners.forEach((listener) => {
        try {
            listener();
        }
        catch (error) {
            console.error('Error notifying state listener:', error);
        }
    });
};
const readLocalJson = (key, fallback) => {
    try {
        const raw = window.localStorage.getItem(getUserScopedStorageKey(key));
        if (!raw) {
            return fallback;
        }
        return JSON.parse(raw);
    }
    catch {
        return fallback;
    }
};
const writeLocalJson = (key, value) => {
    try {
        window.localStorage.setItem(getUserScopedStorageKey(key), JSON.stringify(value));
    }
    catch {
        //  (,  )
    }
};
const getApiBase = () => {
    const envBase = (process.env.MINIAPP_API_BASE ?? '').trim();
    const runtimeBase = typeof window !== 'undefined' && window.__MAX_API_BASE__;
    const base = envBase || runtimeBase || (typeof window !== 'undefined' ? window.location.origin : '');
    return base.replace(/\/+$/, '');
};
const buildEndpoint = (path) => {
    const base = getApiBase();
    return `${base}${path}`;
};
const updateSyncStatus = (patch) => {
    syncStatus = { ...syncStatus, ...patch };
    notifyListeners();
};
const performSync = async () => {
    if (activeUserId === DEFAULT_USER_ID) {
        return;
    }
    if (syncStatus.isSyncing) {
        syncPending = true;
        return;
    }
    updateSyncStatus({ isSyncing: true, error: undefined });
    try {
        const response = await fetch(buildEndpoint(`/api/user/${encodeURIComponent(activeUserId)}/state`), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify({
                activityData,
                homeState,
                social: socialState,
            }),
        });
        if (!response.ok) {
            throw new Error(`Sync failed with status ${response.status}`);
        }
        const payload = (await response.json());
        if (payload.activityData) {
            activityData = cloneActivityData(payload.activityData);
            writeLocalJson('activity_data', activityData);
        }
        if (payload.homeState) {
            homeState = cloneHomeState(payload.homeState);
            writeLocalJson('home_state', homeState);
        }
        if (payload.social) {
            socialState = cloneSocialState(payload.social);
            writeLocalJson('social_state', socialState);
        }
        updateSyncStatus({
            isSyncing: false,
            lastSyncedAt: payload.updatedAt ?? new Date().toISOString(),
            error: undefined,
        });
    }
    catch (error) {
        console.error('Failed to sync user state:', error);
        updateSyncStatus({
            isSyncing: false,
            error: error instanceof Error ? error.message : 'Unknown sync error',
        });
    }
    finally {
        syncStatus.isSyncing = false;
        if (syncPending) {
            syncPending = false;
            scheduleSync();
        }
    }
};
const scheduleSync = () => {
    if (activeUserId === DEFAULT_USER_ID) {
        return;
    }
    if (syncTimeout) {
        window.clearTimeout(syncTimeout);
    }
    syncTimeout = window.setTimeout(() => {
        syncTimeout = undefined;
        void performSync();
    }, SYNC_DEBOUNCE_MS);
};
export const initializeUserStateSync = async () => {
    if (initialized) {
        return;
    }
    const user = getActiveUser();
    activeUserId = user.userId ?? DEFAULT_USER_ID;
    activityData = cloneActivityData(readLocalJson('activity_data', {}));
    homeState = cloneHomeState(readLocalJson('home_state', DEFAULT_HOME_STATE));
    socialState = cloneSocialState(readLocalJson('social_state', { ...DEFAULT_SOCIAL_STATE }));
    if (activeUserId === DEFAULT_USER_ID) {
        initialized = true;
        return;
    }
    try {
        const response = await fetch(buildEndpoint(`/api/user/${encodeURIComponent(activeUserId)}/state`), {
            method: 'GET',
            credentials: 'include',
        });
        if (response.ok) {
            const payload = (await response.json());
            if (payload.activityData) {
                activityData = cloneActivityData(payload.activityData);
                writeLocalJson('activity_data', activityData);
            }
            if (payload.homeState) {
                homeState = cloneHomeState(payload.homeState);
                writeLocalJson('home_state', homeState);
            }
            if (payload.social) {
                socialState = cloneSocialState(payload.social);
                writeLocalJson('social_state', socialState);
            }
            updateSyncStatus({
                isSyncing: false,
                lastSyncedAt: payload.updatedAt ?? null,
                error: undefined,
            });
            notifyStateChange();
        }
        else {
            console.warn(`Failed to load remote state: ${response.status}`);
        }
    }
    catch (error) {
        console.warn('Unable to load remote user state, using local cache:', error);
        updateSyncStatus({
            isSyncing: false,
            error: error instanceof Error ? error.message : 'Failed to load remote state',
        });
    }
    finally {
        initialized = true;
        notifyStateChange();
    }
};
export const getActivityState = () => cloneActivityData(activityData);
export const setActivityState = (data) => {
    activityData = cloneActivityData(data);
    writeLocalJson('activity_data', activityData);
    scheduleSync();
    notifyStateChange();
};
export const getHomeState = () => cloneHomeState(homeState);
export const setHomeState = (state) => {
    homeState = cloneHomeState(state);
    writeLocalJson('home_state', homeState);
    scheduleSync();
    notifyStateChange();
};
export const getSocialState = () => cloneSocialState(socialState);
export const setSocialState = (state) => {
    socialState = cloneSocialState(state);
    writeLocalJson('social_state', socialState);
    scheduleSync();
    notifyStateChange();
};
export const subscribeToSyncStatus = (listener) => {
    listeners.add(listener);
    listener({ ...syncStatus });
    return () => {
        listeners.delete(listener);
    };
};
export const getSyncStatus = () => ({ ...syncStatus });
export const forceSyncNow = async () => {
    if (activeUserId === DEFAULT_USER_ID) {
        return;
    }
    if (syncTimeout) {
        window.clearTimeout(syncTimeout);
        syncTimeout = undefined;
    }
    await performSync();
};
export const subscribeToUserStateChanges = (listener) => {
    stateListeners.add(listener);
    return () => {
        stateListeners.delete(listener);
    };
};
export const getApiBaseUrl = () => getApiBase();
export const buildApiUrl = (path) => buildEndpoint(path);
